footer a{
  color: #fff;
  -webkit-transition: all 0.7s ease;
  -o-transition: all 0.7s ease;
  transition: all 0.7s ease;
}
footer a:hover{
  color: #007bff;
}
.social{
  font-size: 1.3em;
}
.copy{
  text-align: right;
}
@media (max-width: 600px) {
  .foot{
    width: 100% !important;
    -ms-flex-pack: center !important;
        justify-content: center !important;
  }
  .copy{
    text-align: center;
  }
}



