@import url('https://fonts.googleapis.com/css?family=Roboto:300');


.head-img {
  background-image: url("https://i.imgur.com/ADe4Ifx.jpg");
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
}
.header-lead{
  color: #131313;
  letter-spacing: .15em;
  text-shadow: 1px -1px 0 #767676, -1px 2px 1px #737272, -2px 4px 1px #767474, -3px 6px 1px #787777, -4px 8px 1px #7b7a7a, -5px 10px 1px #7f7d7d, -6px 12px 1px #828181, -7px 14px 1px #868585, -8px 16px 1px #8b8a89, -9px 18px 1px #8f8e8d, -10px 20px 1px #949392, -11px 22px 1px #999897, -12px 24px 1px #9e9c9c, -13px 26px 1px #a3a1a1;
}
div h1{
  font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
  font-size: 4em;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
}
@media (max-width: 600px) {
  .head-img{
    height: 0;
  }
  div h1{
    font-size: 2.2em
  }

}



